<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="" v-model="keywords" clearable @clear="clearInput">
            <el-button slot="append" icon="el-icon-search" class="btn" @click="searchUser"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button class="bgc text-white" @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="userList" border stripe class="mt-10">
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="username"></el-table-column>
        <el-table-column label="" prop="email"></el-table-column>
        <el-table-column label="" prop="phone"></el-table-column>
        <el-table-column label="" prop="role"></el-table-column>
        <el-table-column label="" prop="status">
          <template slot-scope="scope">
            <el-switch
              @change="changeStatus(scope.row)"
              v-model="scope.row.status"
              active-color="#16C2C2"
            ></el-switch>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <!--  -->
            <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
            <!--  -->
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="removeuser(scope.row.id)"
            ></el-button>
            <!--  -->

            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="dialogVisible = true,user.user_id = scope.row.id,user.role = scope.row.role 
                options.forEach(item=>item.label==scope.row.role?item.disabled = true:item.disabled=false)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        hide-on-single-page
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page"
        :page-sizes="[2,5,8,10]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addDialogClose">
      <!--  -->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
        <el-form-item label="" prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="addForm.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="addForm.phone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="role">
          <el-input v-model="addForm.role"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </span>
    </el-dialog>
    <!--  -->

    <el-dialog title="" :visible.sync="dialogVisible" width="30%">
      <el-select v-model="value" placeholder="">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
          :disabled="item.disabled"
        ></el-option>
      </el-select>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="changeRole"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  created() {
    this.getUserList()
  },
  data() {
    // 
    var checkPhone = (rules, value, cb) => {
      const regPhone = /^1[34578]\d{9}$/
      if (regPhone.test(value)) {
        return cb() //
      }
      cb(new Error(''))
    }
    return {
      addForm: {
        username: '',
        password: '',
        phone: '',
        role: ''
      }, //
      addFormRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 1,
            max: 11,
            message: ' 1 11',
            trigger: 'blur'
          }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 6,
            max: 15,
            message: ' 6 15',
            trigger: 'blur'
          }
        ],
        phone: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 10,
            max: 11,
            message: '11',
            trigger: 'blur'
          },
          { validator: checkPhone, trigger: 'blur' }
        ],
        role: [{ required: true, message: '', trigger: 'blur' }]
      },
      value: '',
      label: '',
      user: {
        user_id: '',
        role: ''
      },
      options: [
        { value: '1', label: '', disabled: false },
        { value: '2', label: '', disabled: false },
        { value: '3', label: '', disabled: false }
      ],
      dialogVisible: false, //
      addDialogVisible: false, //
      userList: [],
      page: 1,
      pageSize: 5,
      total: 10,
      keywords: ''
    }
  },
  methods: {
    async changeRole() {
      // JSON.stringify(this.user)
      let user = JSON.stringify(this.user)
      let u = JSON.parse(user)
      console.log(this.value)
      if (this.value == '1') {
        u.role = 1
      } else if (this.value == '2') {
        u.role = 2
      } else {
        u.role = 0
      }
      // let user = JSON.stringify(this.user)

      const { data: res } = await this.$http.post('/changeRole', u)
      // console.log(JSON.stringify(this.user))
      // console.log(res)
      if (res.msg !== '') {
        this.$Message.Error('')
      }
      this.$Message.success('')
      this.dialogVisible = false
      this.getUserList()
      this.value = ''
    },
    handleSizeChange(newsize) {
      //pagesize 
      console.log(newsize)
      this.pageSize = newsize
      this.getUserList()
    },
    //
    handleCurrentChange(newpage) {
      this.page = newpage
      console.log(newpage)
      this.getUserList()
    },
    //
    changeStatus(userinfo) {
      let status = userinfo.status == true ? 1 : 0
      let user_id = userinfo.id
      this.changuserStatus({ user_id: user_id, status: status })
    },
    async getUserList() {
      const { data: res } = await this.$http.get(
        '/uerslist/' + this.page + '/' + this.pageSize
      )
      console.log(res)
      // let list = []
      res.data.data.list.forEach(item => {
        item.status === 1 ? (item.status = true) : (item.status = false)
        if (item.role == 2) {
          item.role = ''
          // item.status = true
        } else if (item.role == 1) {
          item.role = ''
        } else {
          item.role = ''
        }
      })
      this.userList = res.data.data.list
      this.total = res.data.data.total
    },
    //
    async changuserStatus(parmas) {
      const { data: res } = await this.$http.post('/editUserStatus', parmas)
      console.log(res)
    },
    //
    async searchUser() {
      let parmas = {
        keyword: this.keywords,
        page: this.page
      }
      const { data: res } = await this.$http.post('/search/user', parmas)
      console.log(this.keywords)
      this.userList = res.data.list
      this.total = res.data.list.length
    },
    //
    clearInput() {
      this.getUserList()
    },
    //
    addDialogClose() {
      this.$refs.addFormRef.resetFields()
    },

    //
    addUser() {
      this.$refs.addFormRef.validate(async vailde => {
        //
        // console.log(vailde)
        if (!vailde) return
        //
        const { data: res } = await this.$http.post('adduser', this.addForm)
        console.log(res)
        if (res.msg !== '') {
          this.$Message.Error('')
        }
        this.$Message.success('')
        this.addDialogVisible = false
        this.getUserList()
      })
    },
    //id
    async removeuser(id) {
      // 
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      //
      console.log(confirmResult)
      if (confirmResult != 'confirm') {
        return this.$Message.info('')
      }
      console.log('')
      const { data: res } = await this.$http.post('/deleteuser', {
        user_id: id
      })
      if (res.msg !== '') {
        this.$Message.Error('')
      }
      this.$Message.success('')
      this.getUserList()
    }
  }
}
</script>
<style lang="less" scoped>
.el-button--primary {
  color: #fff;
  background-color: rgb(22, 194, 194) !important;
  border-color: rgb(22, 194, 194) !important;
}
.el-button:active {
  color: #ffffff !important;
  border-color: #1da3a3 !important;
  outline: 0;
}
.el-button:focus,
.el-button:hover {
  color: #c6e2ff !important;
}
</style>