import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from './components/Login.vue'
import Home from './components/home.vue'
import Welcome from './components/welcome.vue'
import Users from './components/users/users.vue'
import Rights from './components/power/Rights.vue'
import Post from './components/posts/Post.vue'
import Cotent from './components/posts/Cotent.vue'
import Sensitive from './components/sensitive/Sensitive.vue'
import Topic from './components/topic/Topic.vue'
import Count from './components/count/Count.vue'
Vue.use(VueRouter)


const router = new VueRouter({
  routes: [
    { path: '/', redirect: '/login' },
    { path: '/login', component: Login },
    {
      path: '/home', component: Home,
      redirect: '/count',
      children: [
        { path: '/welcome', component: Welcome },
        { path: '/users', component: Users },
        { path: '/rights', component: Rights },
        { path: '/posts', component: Post },
        { path: '/cotent', component: Cotent },
        { path: '/sensitive', component: Sensitive },
        { path: '/topic', component: Topic },
        { path: '/count', component: Count },
      ]
    }
  ]
})
router.beforeEach((to, from, next) => {
  //to
  //from 
  //next 
  //next('/login') 
  if (to.path == '/login') return next();
  //token
  const tokenStr = window.sessionStorage.getItem('token')
  if (!tokenStr) return next('/login') //token
  next() //
})
export default router