<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="" v-model="keywords" clearable >
            <el-button slot="append" icon="el-icon-search" class="btn"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button class="bgc text-white" @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="postlist" border stripe class="mt-10">
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="title"></el-table-column>
        <el-table-column label="" prop="user.username"></el-table-column>
        <el-table-column label="" prop="postname"></el-table-column>
        <el-table-column label="" prop="comment_count"></el-table-column>
        <el-table-column label="" prop="ding_count"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">{{scope.row.create_time|formateDate}}</template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <!--  -->
            <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
            <!--  -->
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="removePostClass(scope.row.id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>
<script>
import moment from '@/assets/js/moment.min.js'
export default {

  data() {
    return {
      postlist: [],
      page: 1,
      pageSize: 5,
      total: 10,
      keywords: ''
    }
  },
  created() {
    this.getpostList()
  },
  filters: {
    formateDate(value) {
        return moment(value*1000).format('YYYY-MM-DD HH:mm:ss');
    }
  },
  methods: {
    async getpostList() {
      const { data: res } = await this.$http.get(
        '/getpost' + '/' + this.page + '/' + this.pageSize
      )
      let list = await this.getpostclass()
      // console.log( list)
      res.data.data.forEach(item => {
        list.forEach(i => {
          if (item.post_class_id == i.id) {
            item.postname = i.classname
          }
        })
      })
      this.postlist = res.data.data
      console.log(res.data.data)
    },
    //
    async getpostclass() {
      const { data: res } = await this.$http.get('/postclass')
      return res.data.list
    }
  }
}
</script>
<style lang="less" scoped>
.el-button--primary {
  color: #fff;
  background-color: rgb(22, 194, 194) !important;
  border-color: rgb(22, 194, 194) !important;
}
</style>