<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="4">
          <el-card :body-style="{ padding: '0px' }">
            <div class="pd-1">
              <span></span>
              <div class="pd-1">
                <div class="pd-1 text-center font-sm font-weight-bold">{{total.postcount}}</div>
                <div class="text-right pr-10 text-light-muted font-smaller">+{{total.newpost}}</div>
              </div>
            </div>
          </el-card>
        </el-col>
        <el-col :span="4">
          <el-card :body-style="{ padding: '0px' }">
            <div class="pd-1">
              <span></span>
              <div class="pd-1">
                <div class="pd-1 text-center font-sm font-weight-bold">{{total.commentcount}}</div>
                <div class="text-right pr-10 text-light-muted font-smaller">+{{total.newcomment}}</div>
              </div>
            </div>
          </el-card>
        </el-col>
        <el-col :span="4">
          <el-card :body-style="{ padding: '0px' }">
            <div class="pd-1">
              <span></span>
              <div class="pd-1">
                <div class="pd-1 text-center font-sm font-weight-bold">{{total.userCount}}</div>
                <div class="text-right pr-10 text-light-muted font-smaller">+{{total.newuser}}</div>
              </div>
            </div>
          </el-card>
        </el-col>
        <el-col :span="4">
          <el-card :body-style="{ padding: '0px' }">
            <div class="pd-1">
              <span></span>
              <div class="pd-1">
                <div class="pd-1 text-center font-sm font-weight-bold">50</div>
                <div class="text-right pr-10 text-light-muted font-smaller">+2</div>
              </div>
            </div>
          </el-card>
        </el-col>
      </el-row>
      <!--  ECharts  DOM -->
      <el-row>
        <el-col :span="12" class="mt-20">
          <el-button type="info" autofocus plain @click="getTime('d')"></el-button>
          <el-button type="info" plain @click="getTime('yesterday')"></el-button>
          <el-button type="info" plain @click="getTime('w')"></el-button>
          <el-button type="info" plain @click="getTime('lastweek')"></el-button>
          <el-button type="info" plain @click="getTime('m')"></el-button>
          <el-button type="info" plain @click="getTime('lastmonth')"></el-button>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="8">
          <div class="mt-20" id="main" style="width:600px;height:400px;"></div>
        </el-col>
        <el-col :span="8">
          <div class="mt-20 pl-20" id="main2" style="width:600px;height:400px;"></div>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>
<script>
import echarts from 'echarts'
import moment from '../../assets/js/moment.min.js'
export default {
  data() {
    return {
      time: 'd',
      xAxis: [],
      y: [],
      total: {},
      userCount: {},
      postCount: {},
      commentCount: {},
      pie: [
        { value: 0, name: '' },
        { value: 0, name: '' },
        { value: 0, name: '' },
        { value: 0, name: '' },
        { value: 0, name: '' },
        { value: 0, name: '' },
        { value: 0, name: '' },
        { value: 0, name: '' },
        { value: 0, name: '' },
        { value: 0, name: '' }
      ]
    }
  },
  created() {
    // console.log(this.getAllDate('2019-3-1', '2019-3-3'))
    // this.getData()
    this.getTotal()
    this.getData('today')
    this.getPieData()
    // this.xAxis.push(now, yesterday)
  },
  mounted() {},
  methods: {
    myChart() {
      // domecharts
      var myChart = echarts.init(document.getElementById('main'))
      var option = {
        title: {
          text: ''
        },
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: ['']
        },
        xAxis: {
          data: ['', '', '']
        },
        yAxis: {},
        series: [
          {
            name: '',
            type: 'bar',
            showBackground: true,
            data: this.y,
            itemStyle: {
              normal: {
                label: {
                  show: true,
                  position: 'top',
                  textStyle: {
                    color: 'black',
                    fontSize: 16
                  }
                },
                color: function(params) {
                  let colorList = ['#c23531', '#2f4554', '#61a0a8']
                  return colorList[params.dataIndex]
                }
              }
            }
          }
        ]
      }
      myChart.setOption(option)
    },
    myPie() {
      var myChart = echarts.init(document.getElementById('main2'))
      var option = {
        title: {
          text: 'APP',
          subtext: '',
          left: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c} ({d}%)'
        },
        legend: {
          orient: 'vertical',
          left: 'right',
          data: [
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            ''
          ]
        },
        series: [
          {
            name: '',
            type: 'pie',
            radius: '55%',
            center: ['50%', '60%'],
            data: this.pie,
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      }
      myChart.setOption(option)
    },
    async getData(time) {
      let params = {
        data: time
      }
      // console.log(params.data)
      const { data: res } = await this.$http.post('/getCount', params)
      this.y = []
      this.y.push(
        res.data.data.userCount,
        res.data.data.postCount,
        res.data.data.commentCount
      )
      // console.log(this.y)
      this.myChart()
    },
    getTime(time) {
      this.time = time
      this.getData(time)
    },
    async getPieData() {
      const { data: res } = await this.$http.get('/getUserinfo')
      console.log(res)
      res.data.data.path.forEach(item => {
        if (item === '') {
          this.pie[0].value++
        }
        if (item === '') {
          this.pie[1].value++
        }
        if (item === '') {
          this.pie[2].value++
        }
        if (item === '') {
          this.pie[3].value++
        }
        if (item === '') {
          this.pie[4].value++
        }
        if (item === '') {
          this.pie[5].value++
        }
        if (item === '') {
          this.pie[6].value++
        }
        if (item === '') {
          this.pie[7].value++
        }
        if (item === '') {
          this.pie[8].value++
        }
        if (item === '') {
          this.pie[9].value++
        }
      })
      this.myPie()
      console.log(this.pie)
    },
    //
    async getTotal() {
      const { data: res } = await this.$http.get('/getCount')
      // console.log(res)
      this.total = res.data.data
    }
  }
}
</script>
 <style lang="less" scoped>
.time {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.image {
  width: 100%;
  display: block;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: '';
}

.clearfix:after {
  clear: both;
}
.count {
  display: flex;
  align-items: center;
  padding: 10px;
}
</style>
