<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="topicList" border stripe class="mt-10">
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="title"></el-table-column>
        <el-table-column label="" prop="desc"></el-table-column>
        <el-table-column label="" prop="topicClass"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <!--  -->
            <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
            <!--  -->
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="removePostClass(scope.row.id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <!--  -->
      <el-pagination
        hide-on-single-page
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page"
        :page-sizes="[2,5,8,10]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
      <!--  -->
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="addDialogVisible"
        width="50%"
        @close="addDialogClose"
      >
        <!--  -->
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="80px">
          <el-form-item label="" prop="classname">
            <el-input v-model="addForm.classname"></el-input>
          </el-form-item>
          <el-form-item label="" prop="classname">
            <el-input v-model="addForm.classname"></el-input>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false"> </el-button>
          <el-button type="primary" > </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      addForm: {
        classname: ''
      },
      addFormRules: {
        classname: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      addDialogVisible: false,
      topicList: [],
      page: 1,
      pageSize: 5,
      total: 10
    }
  },
  created() {
    this.getPostClassList()
    // this.gettopicClass()
  },
  //
  methods: {
    handleSizeChange(newsize) {
      //pagesize 
      console.log(newsize)
      this.pageSize = newsize
      // this.getUserList()
      this.getPostClassList()
    },
    //
    handleCurrentChange(newpage) {
      this.page = newpage
      console.log(newpage)
      // this.getUserList()
      this.getPostClassList()
    },
    async getPostClassList() {
      // const { data: res } = await this.$http.get('/topicclass')
      const { data: res } = await this.$http.get(
        '/getTopic/' + this.page + '/' + this.pageSize
      )
      let list =  await this.gettopicClass()
      res.data.data.list.forEach(item=>{
        list.forEach(i=>{
          if(item.topic_class_id == i.id){
            item.topicClass = i.classname
          }
        })
      })
      // console.log(res.data.list)
      this.total = res.data.data.total
      this.topicList = res.data.data.list
    },
    // addDialogClose() {
    //   this.$refs.addFormRef.resetFields()
    // },
      //
    clearInput() {
      this.getUserList()
    },
    //
    addDialogClose() {
      this.$refs.addFormRef.resetFields()
    },
    async gettopicClass (){
      const { data: res } = await this.$http.get('/topicclass')
      // console.log(res.data.list)
      
      return res.data.list
    },
    // async addPostClass() {
    //   // console.log(this.classname)
    //   const { data: res } = await this.$http.post('/addPostClass', {
    //     classname: this.addForm.classname
    //   })
    //   if (res.msg !== '') {
    //     this.$Message.Error('')
    //   }
    //   this.$Message.success('')
    //   this.addDialogVisible = false
    //   this.getPostClassList()
    // },
    async removePostClass(id) {
      // 
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$Message.info('')
      }
      // console.log(id)
      const { data: res } = await this.$http.post('/deletPostClass', { id: id })
      if (res.msg !== '') {
        this.$Message.Error('')
      }
      this.$Message.success('')
      this.addDialogVisible = false
      this.getPostClassList()
    }
  }
}
</script>
<style lang="less" scoped>
.el-button--primary {
  color: #fff;
  background-color: rgb(22, 194, 194) !important;
  border-color: rgb(22, 194, 194) !important;
}
</style>